.block__row {
	border: 10px solid darkgreen;
}

.block__column {
	border: 10px solid salmon;
}

.block__item {
	background-color: darkseagreen;
	padding: 10px;
	font-size: 20px;
	color: #000;
	font-weight: 700;
}

.flex_cont {
	display: flex;
}

.in_flex_cont {
	display: inline-flex;
}

.jcs {
	justify-content: flex-start;
}

.jce {
	justify-content: flex-end;
}

.jcc {
	justify-content: center;
}